.navigation-user {
  grid-column: 1/3;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    grid-column: 1/4;
    padding: 0 0 0 47px;
    margin: 0;
  }
}

.navigation-user__item {
  display: flex;
  align-items: center;
}

.navigation-user__phone {
  display: block;
  padding: 15px;
  background-color: @secondary-background-dark;
  border-radius: 50%;
}

.navigation-user__email-wrapper {
  position: relative;
  width: 45px;
  height: 45px;
  background-color: @secondary-background-dark;
  border-radius: 50%;
}

.navigation-user__email {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation-user__link {
  margin-left: 16px;
  font-family: "Blogger Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  line-height: 18px;
  letter-spacing: 0;
  color: @main-background-dark;
  text-decoration: none;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 28px;
    line-height: 28px;
  }
}
